#myRange2 {
  /*removes default webkit styles*/
  -webkit-appearance: none;

  /*fix for FF unable to apply focus style bug */
  border: 1px solid white;

  /*required for proper track sizing in FF*/
  width: 200px;
}

#myRange2::-webkit-slider-runnable-track {
  width: 200px;
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}
#myRange2::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #01FF70;
  margin-top: -4px;
}
#myRange2:focus {
  outline: none;
}
#myRange2:focus::-webkit-slider-runnable-track {
  background: #ccc;
}

#myRange2::-moz-range-track {
  width: 200px;
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}
#myRange2::-moz-range-thumb {
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #01FF70;
}

/*hide the outline behind the border*/


#myRange2:-moz-focusring{
  outline: 1px solid white;
  outline-offset: -1px;
}

#myRange2::-ms-track {
  width: 200px;
  height: 5px;

  /*remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead */
  background: transparent;

  /*leave room for the larger thumb to overflow with a transparent border */
  border-color: transparent;
  border-width: 6px 0;

  /*remove default tick marks*/
  color: transparent;
}
#myRange2::-ms-fill-lower {
  background: #777;
  border-radius: 10px;
}
#myRange2::-ms-fill-upper {
  background: #ddd;
  border-radius: 10px;
}
#myRange2::-ms-thumb {
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #01FF70;
}
#myRange2:focus::-ms-fill-lower {
  background: #888;
}
#myRange2:focus::-ms-fill-upper {
  background: #ccc;
}

